@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&family=Roboto:wght@300;400;500&display=swap);
*{padding:0;margin:0;font-family:'Roboto', sans-serif}html{color:#333;background:#cdc url("https://i.imgur.com/LtdNdBp.jpg") repeat top left}.App{font-family:sans-serif;text-align:center;margin:6rem 0}h1{font-size:5em;text-decoration:underline;font-family:'Merriweather', serif}.app_description{line-height:34px;font-size:1.4em;margin-left:1%}.paragraph_holder,.directions_holder,.note_holder{display:flex;justify-content:space-evenly;align-items:center;padding:2%;outline:none}.main_h3{font-size:1.8em}.recipe_img{width:35%}.tool_img,.hat_img{width:20%}.message_h2{font-size:3.2em}

.searchForm{display:grid;grid-template-columns:auto 1fr auto;grid-gap:1rem;align-items:center;margin-top:7%;padding:2%}.label{font-size:1.2rem;margin-bottom:0.2rem}.input{font-size:1.6rem;padding:0.5rem 2rem;line-height:2.8rem;border-radius:20px;border:1px solid #ddd;margin-bottom:1rem}.search_button{background-color:rgba(0,0,0,0.75);color:white;padding:1rem 2rem;border:1px solid rgba(0,0,0,0.75);border-radius:20px;font-size:1.4rem;cursor:pointer;transition:background-color 250ms}.recipe_list{margin-top:2rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 4fr));grid-template-rows:auto;grid-gap:3%;padding:1%}.recipe{padding:1rem 3rem;border-radius:10px;box-shadow:3px 7px 12px 2px #5d5d5d;margin-bottom:2rem;background-color:#a9a9a95c;min-height:88%}.recipe_content{display:flex;flex-direction:column}.recipe_title{margin-bottom:0.5rem;font-size:1.4rem}.recipe_image{width:70%}.recipe_favorite{font-size:1.2em;margin-top:5%;padding:2%}.recipe_info{font-size:1.2em}

.modal_overlay{position:fixed;top:0;right:0;bottom:0;left:0;display:flex;align-items:center;justify-content:center;z-index:98;height:100%;width:100%;background-color:#4140404d}.modal{position:relative;z-index:99;width:100%;max-width:20rem;max-height:100%;margin:0 auto}.modal_close{position:absolute;top:-1.5rem;right:0;padding:5px;border:0;-webkit-appearance:none;background:none;color:white;cursor:pointer}.modal_body{padding:1.25rem 1.5rem;border-radius:4px;background-color:white}

